import Home from "@/views/home";
import Login from "@/views/login";
import Setting from "@/views/module/other/setting";
import Lock from "@/views/module/other/lock";
import Main from "@/views/module/demo/main";
import Demo1 from "@/views/module/demo/demo1";
import Demo2 from "@/views/module/demo/demo2";
import Test1 from "@/views/module/demo/test1";
import Test2 from "@/views/module/demo/test2";

export default [
  {
    path: "/",
    hidden: true,
    icon: "menu",
    title: "Home",
    component: Home,
    children: [
      { path: "/", icon: "menu", component: Main, name: "Home" },
      {
        path: "setting",
        icon: "menu",
        component: Setting,
        name: "Setting"
      }
    ]
  },
  {
    path: "/demo",
    icon: "menu",
    title: "Demo",
    component: Home,
    children: [
      { path: "demo1", icon: "menu", component: Demo1, name: "Demo1" },
      { path: "demo2", icon: "menu", component: Demo2, name: "Demo2" }
    ]
  },
  {
    path: "/test",
    icon: "menu",
    title: "Test",
    component: Home,
    children: [
      { path: "test1", icon: "menu", component: Test1, name: "Test1" },
      { path: "test2", icon: "menu", component: Test2, name: "Test2" }
    ]
  },
  {
    path: "/login",
    hidden: true,
    title: "Login",
    name: "Login",
    component: Login
  },
  {
    path: "/lock",
    hidden: true,
    title: "Lock",
    name: "Lock",
    component: Lock
  },
  {
    path: "*",
    hidden: true,
    redirect: "/"
  }
];



