import Router from "vue-router";
import BaseRouter from "@/router/modules/baseRouter";
import Cookie from "js-cookie";

let routerObj = new Router({
  mode: "history",
  routes: [].concat(BaseRouter)
});

//不进行缓存的页面
let excludePages = ["Login", "Lock"];

//加入页面缓存
let keepPage = function(to) {
  if (excludePages.indexOf(to.name) === -1) {
    let vueStore = routerObj.app.$store;
    if (!routerObj.app.$store.getters.sysConfig.singlePage) {
      vueStore.dispatch("base/addTabPagesName", {
        name: to.name,
        path: to.path
      });
    }
  }
};

//页面跳转之前拦截
routerObj.beforeEach((to, from, next) => {
  //登陆信息验证
  if (to.path !== "/login") {
    if (!Cookie.get("user")) {
      next({ path: "/login" });
      return;
    } else {
      keepPage(to);
    }
  }
  next();
});

export default routerObj;



