<template>
  <el-tabs v-model="activeName">
    <el-tab-pane name="theme">
      <span slot="label">
        <i class="alibaba al-skin"></i>
        <span>主题管理</span>
      </span>
      <el-button v-for="item in $store.state.base.sysConfig.theme" @click='$store.dispatch("base/changeTheme",item)' :key="item" :type="$store.state.base.sysConfig.currTheme===item?'primary':''" plain>{{item}}</el-button>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
export default {
  data() {
    return {
      activeName: "theme"
    };
  },
  methods: {}
};
</script>
<style lang="scss" scoped>
.el-button {
  margin: 5px;
}
</style>



