<template>
  <div class='lock-page'>
    <div class='lock-login-area'>
      <transition name="bounce" tag="div">
        <div v-if='showImg' class='userImg' @click="showPW = true" :style="showPW?'left:0px;':'left:125px;'">
          <i class="alibaba al-yonghu2"></i>
        </div>
      </transition>
      <transition name="rotateDownLeft" tag="div">
        <div class='inputPW' v-if='showPW'>
          <el-input :placeholder="$t('login.passWd')" v-model="passwd">
            <el-button @click="lockLogin" slot="append" icon="alibaba al-denglu" :title="$t('login.button')"></el-button>
          </el-input>
        </div>
      </transition>
    </div>
    <transition name="bounce" tag="div">
      <p v-if='showImg'>已锁定</p>
    </transition>
  </div>
</template>
<script>
export default {
  data() {
    return {
      passwd: null,
      showImg: false,
      showPW: false
    };
  },
  mounted: function() {
    this.$store.dispatch("base/changeTheme");
    this.$nextTick(() => {
      //路由切换动画1000毫秒，所以延迟500毫秒，解决动画不全的问题
      setTimeout(() => {
        this.showImg = true;
      }, 500);
    });
  },
  methods: {
    lockLogin() {
      this.$router.push({ path: "/" });
    }
  }
};
</script>




