<template>
  <el-container v-loading="loading" :element-loading-text="$t('loading')" element-loading-spinner="alibaba al-jiazai1" element-loading-background="rgba(0, 0, 0, 0.6)">
    <vue-progress-bar></vue-progress-bar>
    <sidebar></sidebar>
    <el-container>
      <navbar></navbar>
      <app-main></app-main>
    </el-container>
  </el-container>
</template>

<script>
import { Navbar, Sidebar, AppMain } from "./module/layout";

export default {
  components: { Navbar, Sidebar, AppMain },
  data() {
    return {
      loading: true
    };
  },
  mounted: function() {
    //通知vuex注册窗口改变事件
    this.$store.dispatch("base/monitorWinSize");
    //主题初始化操作
    this.$store.dispatch("base/changeTheme");
    //获取后台初始化信息
    this.$store.dispatch("base/initSystem").then(resp => {
      this.loading = false;
    });
  }
};
</script>




