import config from "../../resources/appConfig";
import router from "../../router/index";
import system from "../uiInterface";
import Cookie from "js-cookie";

let defaultTabsData = {
  currTabName: "/",
  tabPagesName: [
    {
      name: "Home",
      path: "/"
    }
  ]
};

export default {
  namespaced: true,
  state: {
    //动态数据
    dynData: {
      browserWidth: null,
      browserHeight: null
    },
    //系统配置
    sysConfig: config,
    //---------------------用于配置布局的全局变量开始-------------------
    //响应式布局
    defaultLayout: null,
    //导航栏是否展开
    isCollapse: false,
    //多页面时记录tabs数据
    tabsData: JSON.parse(JSON.stringify(defaultTabsData)),
    //是否为全屏模式
    isFullScreen: false,
    //---------------------用于配置布局的全局变量结束-------------------
    //用户信息，用于所有页面的全局共享
    userInfo: {}
  },
  mutations: {
    setDynData(state, dynData) {
      Object.assign(state.dynData, dynData);
    },
    setTabsData(state, tabsData) {
      Object.assign(state.tabsData, tabsData);
    },
    resetTabsData(state) {
      state.tabsData = JSON.parse(JSON.stringify(defaultTabsData));
    },
    setDefaultLayout(state, defaultLayout) {
      Object.assign(state, defaultLayout);
    },
    setCollapse(state, collapse) {
      Object.assign(state, collapse);
    },
    toggleFullScreen(state) {
      state.isFullScreen = !state.isFullScreen;
    },
    setSysConfig(state, sysconfig) {
      Object.assign(state.sysConfig, sysconfig);
    },
    setUserInfo(state, userInfo) {
      Object.assign(state.userInfo, userInfo);
    }
  },
  actions: {
    //注册浏览器窗口改变事件
    monitorWinSize({ dispatch, state, commit }) {
      if (!window.onresize) {
        window.onresize = () => {
          commit("setDynData", {
            browserWidth: document.documentElement.clientWidth,
            browserHeight: document.documentElement.clientHeight
          });
          dispatch("changeLayoutType", state.dynData.browserWidth);
        };
        window.onresize();
      }
    },
    //删除tab页面
    delTabPagesName({ state, commit }, tabPath) {
      let currindex = state.tabsData.tabPagesName.findIndex(item => {
        return item.path === tabPath.path;
      });
      if (currindex > -1) {
        let tabPagesNamet = Object.assign([], state.tabsData.tabPagesName);
        if (tabPath.path === state.tabsData.currTabName) {
          tabPagesNamet.splice(currindex, 1);
          commit("setTabsData", {
            currTabName: state.tabsData.tabPagesName[currindex - 1].path,
            tabPagesName: tabPagesNamet
          });
          router.push({
            path: state.tabsData.tabPagesName[currindex - 1].path
          });
        } else {
          tabPagesNamet.splice(currindex, 1);
          commit("setTabsData", {
            tabPagesName: tabPagesNamet
          });
        }
      }
    },
    //添加Tab页面
    addTabPagesName({ state, commit }, tabObj) {
      let idx = state.tabsData.tabPagesName.findIndex(item => {
        return item.path === tabObj.path;
      });
      let tabPagesNamet = Object.assign([], state.tabsData.tabPagesName);
      if (idx === -1) tabPagesNamet.push(tabObj);
      commit("setTabsData", {
        currTabName: tabObj.path,
        tabPagesName: tabPagesNamet
      });
    },
    //根据窗口宽度确定布局类型
    changeLayoutType({ state, commit }, browserWidth) {
      if (browserWidth >= state.sysConfig.defaultLayout) {
        commit("setDefaultLayout", { defaultLayout: "defaultLayout" });
        if (state.isCollapse) commit("setCollapse", { isCollapse: false });
      } else if (
        browserWidth < state.sysConfig.streamLayout[0] &&
        browserWidth >= state.sysConfig.streamLayout[1]
      ) {
        commit("setDefaultLayout", { defaultLayout: "streamLayout" });
        if (!state.isCollapse) commit("setCollapse", { isCollapse: true });
      } else if (browserWidth < state.sysConfig.miniLayout) {
        commit("setDefaultLayout", { defaultLayout: "miniLayout" });
        if (!state.isCollapse) commit("setCollapse", { isCollapse: true });
      }
    },
    //全屏模式
    fullScreenModel({ state, commit }) {
      if (state.isFullScreen) {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        }
        //FireFox
        else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        }
        //Chrome等
        else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        }
        //IE11
        else if (document.msExitFullscreen) {
          document.msExitFullscreen();
        }
      } else {
        let docElm = document.documentElement;
        //W3C
        if (docElm.requestFullscreen) {
          docElm.requestFullscreen();
        }
        //FireFox
        else if (docElm.mozRequestFullScreen) {
          docElm.mozRequestFullScreen();
        }
        //Chrome等
        else if (docElm.webkitRequestFullScreen) {
          docElm.webkitRequestFullScreen();
        }
        //IE11
        else if (elem.msRequestFullscreen) {
          elem.msRequestFullscreen();
        }
      }
      commit("toggleFullScreen");
    },
    //关闭其它标签页
    closeOtherTabPage({ dispatch, state, commit }) {
      let currIndex = state.tabsData.tabPagesName.findIndex(item => {
        return item.path === state.tabsData.currTabName;
      });
      if (currIndex > -1) {
        let tabPagesNamet = Object.assign([], state.tabsData.tabPagesName);
        tabPagesNamet.splice(1, currIndex - 1);
        commit("setTabsData", {
          tabPagesName: tabPagesNamet
        });
        dispatch("closeRightTabPage");
      }
    },
    //关闭右侧标签页
    closeRightTabPage({ state, commit }) {
      let currIndex = state.tabsData.tabPagesName.findIndex(item => {
        return item.path === state.tabsData.currTabName;
      });
      if (currIndex > -1) {
        if (currIndex !== state.tabsData.tabPagesName.length - 1) {
          let tabPagesNamet = Object.assign([], state.tabsData.tabPagesName);
          tabPagesNamet.splice(currIndex + 1);
          commit("setTabsData", {
            tabPagesName: tabPagesNamet
          });
        }
      }
    },
    //关闭全部标签页
    closeAllTabPage({ state, commit }) {
      commit("resetTabsData");
      router.push({ path: state.tabsData.currTabName });
    },
    //改变主题
    changeTheme({ state, commit }, themeName) {
      if (themeName) {
        commit("setSysConfig", { currTheme: themeName });
        window.document.body.setAttribute("class", themeName.toLowerCase());
      } else {
        window.document.body.setAttribute(
          "class",
          state.sysConfig.currTheme.toLowerCase()
        );
      }
    },
    //切换国际化
    changeI18n({ commit }, languageCode) {
      commit("setSysConfig", { currLanguage: languageCode });
      Cookie.set("language", languageCode, { expires: 7 });
    },
    //登陆
    login({ commit }, data) {
      setTimeout(() => {
        Cookie.set("user", { username: data.userName }, { expires: 7 });
        commit("setUserInfo", { username: data.userName });
        console.log("password:-->" + data.password);
        router.push({ path: "/" });
      }, 3000);
    },
    //注销
    logout() {
      Cookie.remove("user");
      //清空缓存的vuex状态
      window.localStorage.removeItem("vuex");
      system.saySysSuccess("操作成功!");
      //页面刷新可以重置VUEX
      window.location.reload();
      router.push({ path: "/login" });
    },
    //向后台拉取必要的初始化信息
    initSystem() {
      return new Promise(function(resolve, reject) {
        setTimeout(() => {
          resolve({});
        }, 3000);
      });
    }
  }
};



