<template>
  <div class="login-page">
    <div class='bg-mark'></div>
    <div class='login-mark'>
      <div class='logo'>
        <i class="alibaba al-logo-"></i>
        <p>{{pageInfo.logoTitle}}</p>
      </div>
      <div class='title'>
        <p>{{pageInfo.titleEN}}</p>
        <p>{{pageInfo.titleZH}}</p>
      </div>
      <div class='login-main'>
        <div class='icons'>
          <ul>
            <li>
              <el-dropdown trigger='click' placement='bottom' @command="handleSetLanguage">
                <span class="el-dropdown-link">
                  <span>
                    <i class="alibaba al-yuyanqiehuan" :title="$t('navbar.language')"></i>
                  </span>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item v-for="item in $store.state.base.sysConfig.language" :disabled="$store.state.base.sysConfig.currLanguage===item.code" :command='item.code' :key='item.code'>{{item.title}}</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </li>
          </ul>
        </div>
        <div class='form-area'>
          <p>{{$t('login.title')}}</p>
          <el-form ref="lForm" :model="loginForm" :rules="loginRules">
            <el-form-item prop='userName'>
              <el-input :placeholder="$t('login.piName')" v-model="loginForm.userName">
                <template slot="prepend">
                  <i class="alibaba al-yonghu"></i>
                </template>
              </el-input>
            </el-form-item>
            <el-form-item prop='password'>
              <el-input :placeholder="$t('login.piPW')" :type="showPw?'':'password'" v-model="loginForm.password">
                <template slot="prepend">
                  <i class="alibaba al-jiesuo1"></i>
                </template>
                <el-button @click="showPw=!showPw" class='showPw' slot="append" :icon="showPw?'alibaba al-chakanmima1':'alibaba al-chakanmima'"></el-button>
              </el-input>
            </el-form-item>
            <el-form-item>
              <el-button :loading="loading" type="primary" class='loginBut' @click.native.prevent="submitLoginForm">{{$t('login.button')}}</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
    <div class="copyright">
      {{pageInfo.copyright}}
    </div>
  </div>
</template>
<script>
export default {
  data() {
    let validateUserName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(this.$t("login.piName")));
      }
      if (value.length < 4 || value.length > 20) {
        return callback(new Error(this.$t("login.iwarn1")));
      }
      callback();
    };
    let validatePassword = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(this.$t("login.piPW")));
      }
      if (value.length < 10 || value.length > 30) {
        return callback(new Error(this.$t("login.iwarn2")));
      }
      callback();
    };
    return {
      pageInfo: {
        titleZH: "后台管理系统",
        titleEN: "Content Manage System",
        logoTitle: "SUNRISING",
        copyright: "Copyright © 2094 SUNRISING. All rights reserved."
      },
      loading: false,
      loginForm: {
        userName: null,
        password: null
      },
      showPw: false,
      loginRules: {
        userName: [
          {
            validator: validateUserName,
            trigger: "change"
          }
        ],
        password: [
          {
            validator: validatePassword,
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    //国际化切换
    handleSetLanguage(lang) {
      this.$i18n.locale = lang;
      this.$store.dispatch("base/changeI18n", lang);
    },
    //进行登陆
    submitLoginForm() {
      this.$refs["lForm"].validate(valid => {
        if (valid) {
          this.loading = true;
          this.$store.dispatch("base/login", this.loginForm);
        } else {
          console.log("[loginForm] error submit!!");
          return false;
        }
      });
    }
  }
};
</script>
<style lang='scss'>
.login-page {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-image: url("../assets/login-bg.jpg");
  background-color: #eee;
  background-repeat: no-repeat;
  background-position: center center;
  position: relative;
  & > .bg-mark {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(56, 93, 225, 0.7);
  }
  & > .login-mark {
    position: absolute;
    top: 40px;
    left: 40px;
    right: 40px;
    bottom: 40px;
    background: linear-gradient(
      45deg,
      transparent 20px,
      rgba(255, 255, 255, 0.2) 0
    );
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    & > .logo {
      position: absolute;
      top: 20px;
      left: 20px;
      height: 50px;
      line-height: 50px;
      width: 50%;
      // background-image: url("../assets/logo.png");
      background-size: 35%;
      background-position: 20px center;
      background-repeat: no-repeat;
      & i {
        font-size: 30px;
        color: #fff;
      }
      & > p {
        position: absolute;
        top: 0px;
        display: inline-block;
        line-height: 50px;
        height: 50px;
        margin: 0px;
        font-size: 30px;
        color: #fff;
        margin-left: 10px;
      }
    }
    & > .title {
      position: absolute;
      bottom: 15%;
      right: 30%;
      width: 70%;
      height: 100px;
      & > p {
        margin: 0;
        padding: 0;
        text-align: right;
        height: 50px;
        line-height: 50px;
        padding-right: 20px;
        color: #fff;
        &:nth-child(1) {
          font-size: 40px;
        }
        &:nth-child(2) {
          font-size: 32px;
        }
      }
    }
    & > .login-main {
      position: absolute;
      top: 0px;
      right: 0px;
      bottom: 0px;
      width: 30%;
      min-width: 355px;
      background-color: #fff;
      & > .icons {
        position: absolute;
        top: 20px;
        right: 20px;
        height: 50px;
        width: calc(100% - 40px);
        & > ul {
          height: 100%;
          width: 100%;
          list-style: none;
          text-align: right;
          & > li {
            display: inline-block;
            height: 100%;
            line-height: 50px;
            padding: 0 5px;
          }
        }
      }
      & > .form-area {
        position: absolute;
        right: 10%;
        width: calc(100% - 20%);
        height: 300px;
        bottom: 60%;
        transform: translateY(150px);
        & > p {
          margin: 0px;
          padding: 0px;
          text-align: center;
          font-size: 40px;
          margin-bottom: 50px;
          color: #409eff;
        }
        & .loginBut {
          width: 100%;
          height: 41px;
          padding: 0px;
        }
        & i {
          font-size: 20px;
        }
        & .showPw i {
          font-size: 14px;
        }
      }
    }
  }
  & > .copyright {
    position: absolute;
    bottom: 10px;
    width: 100%;
    color: #fff;
    font-size: 12px;
    text-align: center;
    opacity: 0.5;
  }
}
</style>



