export default {
  //默认布局
  defaultLayout: 948,
  //精简布局
  streamLayout: [948, 500],
  //迷你布局
  miniLayout: 500,
  //是否使用伪多页应用
  singlePage: false,
  //主题列表
  theme: ["Dawn", "Blue", "Dark", "Light"],
  //当前的主题
  currTheme: "Dawn",
  //国际化
  language: [
    {
      title: "中文",
      code: "zh"
    },
    {
      title: "English",
      code: "en"
    }
  ],
  currLanguage: "zh"
};



