<template>
  <div class='contents'>
    <div class='tabs' v-if="!$store.state.base.sysConfig.singlePage">
      <el-tabs v-model="tabPagesName.curr" type="card" @tab-remove="removeTab" @tab-click="clickTab">
        <el-tab-pane v-for="item in tabPagesName.data" :key="item.path" :label="$t('route.'+item.name)" :name="item.path" :closable='item.path!=="/"'></el-tab-pane>
      </el-tabs>
      <el-dropdown>
        <span class="el-dropdown-link">
          <i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="$store.dispatch('base/closeOtherTabPage')">{{$t('document.tabs.closeOtherTags')}}</el-dropdown-item>
          <el-dropdown-item @click.native="$store.dispatch('base/closeRightTabPage')">{{$t('document.tabs.closeTheRightLabel')}}</el-dropdown-item>
          <el-dropdown-item @click.native="$store.dispatch('base/closeAllTabPage')">{{$t('document.tabs.closeAllTags')}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <el-main :style="!$store.state.base.sysConfig.singlePage?'top: 41px;':'top: 0px;'">
      <keep-alive :include='keepRouters'>
        <router-view></router-view>
      </keep-alive>
    </el-main>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters(["tabPagesName", "keepRouters"])
  },
  methods: {
    //点击tab标签
    clickTab(targetObj) {
      this.$router.push({ path: targetObj.name });
    },
    //移除tab标签
    removeTab(targetPath) {
      this.$store.dispatch("base/delTabPagesName", {
        path: targetPath
      });
    }
  }
};
</script>






