import axios from "axios";
import app from "../main";

// `transformRequest` 允许在向服务器发送前，修改请求数据
// 只能用在 'PUT', 'POST' 和 'PATCH' 这几个请求方法
// 后面数组中的函数必须返回一个字符串，或 ArrayBuffer，或 Stream
let transformRequest = function(data) {
  // console.log(data);
  return data;
};

// `transformResponse` 在传递给 then/catch 前，允许修改响应数据
let transformResponse = function(data) {
  // 对 data 进行任意转换处理
  // console.log(data);
  return data;
};

// `onUploadProgress` 允许为上传处理进度事件
let onUploadProgress = function(progressEvent) {
  // 对原生进度事件的处理
};

// `onDownloadProgress` 允许为下载处理进度事件
let onDownloadProgress = function(progressEvent) {
  // 对原生进度事件的处理
};

let ajax = axios.create({
  // `baseURL` 将自动加在 `url` 前面，除非 `url` 是一个绝对 URL。
  // 它可以通过设置一个 `baseURL` 便于为 axios 实例的方法传递相对 URL
  baseURL: "/api/",
  transformRequest: [transformRequest],
  transformResponse: [transformResponse],
  onUploadProgress: onUploadProgress,
  onDownloadProgress: onDownloadProgress,
  // `maxContentLength` 定义允许的响应内容的最大尺寸
  maxContentLength: 2000,
  // `validateStatus` 定义对于给定的HTTP 响应状态码是 resolve 或 reject  promise 。如果 `validateStatus` 返回 `true` (或者设置为 `null` 或 `undefined`)，promise 将被 resolve; 否则，promise 将被 rejecte
  validateStatus: function(status) {
    // return status >= 200 && status < 300; // 默认的
    return status;
  },
  // `timeout` 指定请求超时的毫秒数(0 表示无超时时间)
  // 如果请求话费了超过 `timeout` 的时间，请求将被中断
  timeout: 4000
});

// 添加请求拦截器
ajax.interceptors.request.use(
  config => {
    console.log("request success!");
    app.$Progress.start();
    return config;
  },
  error => {
    console.log("request filed!" + error);
  }
);

// 添加响应拦截器
ajax.interceptors.response.use(
  response => {
    console.log("response success!");
    console.log(response.status);
    app.$Progress.finish();
    return response;
  },
  error => {
    console.log("response filed!" + error);
  }
);

export default ajax;



