<template>
  <el-header height='50px'>
    <ul>
      <li>
        <i @click="$store.commit('base/setCollapse',{isCollapse:!$store.state.base.isCollapse})" :class="!$store.state.base.isCollapse?'el-icon-fa fa-outdent':'el-icon-fa fa-indent'" :title="$t('navbar.telescopicMenu')"></i>
      </li>
    </ul>
    <ul>
      <li>
        <i class="el-icon-fa fa-bell-o"></i>
        <el-badge class="mark" :value="20" />
      </li>
      <li>
        <i @click="$store.dispatch('base/fullScreenModel')" :class="$store.state.base.isFullScreen?'alibaba al-iconzuixiaohua':'alibaba al-zuidahua1'" :title="$store.state.base.isFullScreen?$t('navbar.exitFullScreen'):$t('navbar.enterFullScreen')"></i>
      </li>
      <li>
        <i class="alibaba al-suoping" :title="$t('navbar.lockScreen')" @click="lockScreen"></i>
      </li>
      <li>
        <el-dropdown trigger='click' placement='bottom' @command="handleSetLanguage">
          <span class="el-dropdown-link">
            <span>
              <i class="alibaba al-yuyanqiehuan" :title="$t('navbar.language')"></i>
            </span>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="item in $store.state.base.sysConfig.language" :disabled="$store.state.base.sysConfig.currLanguage===item.code" :command='item.code' :key='item.code'>{{item.title}}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </li>
      <li>
        <el-dropdown @command='userOp'>
          <span class="el-dropdown-link">
            <span v-if='$store.state.base.defaultLayout !== "miniLayout"'>Sun Rising
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <span v-if='$store.state.base.defaultLayout === "miniLayout"'>
              <i class="el-icon-fa fa-user-o"></i>
            </span>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command='perInfor'>{{$t('navbar.perInfor')}}</el-dropdown-item>
            <el-dropdown-item command='changePW'>{{$t('navbar.changePW')}}</el-dropdown-item>
            <el-dropdown-item command='logout' divided>{{$t('navbar.logout')}}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </li>
      <li>
        <i class="el-icon-fa fa-cog" :title="$t('navbar.setting')" @click='$router.push({path: "/setting"})'></i>
      </li>
    </ul>
  </el-header>
</template>
<script>
export default {
  methods: {
    handleSetLanguage(lang) {
      this.$i18n.locale = lang;
      this.$store.dispatch("base/changeI18n", lang);
    },
    lockScreen() {
      this.$router.push({ path: "/lock" });
    },
    //用户下拉
    userOp(op) {
      if (op === "logout") this.$store.dispatch("base/logout");
    }
  }
};
</script>


