/**
 * 为了把vuex和UI库解耦，所以把vueX调用UI显示信息的方法提出来，
 * 同意管理，当更换UI库时修改这些方法的实现即可
 */
import { Message, Notification } from "element-ui";

let duration = 1500;

export default {
  //系统级警告
  saySysWarn(message) {
    Notification({
      title: "警告",
      message: message,
      type: "warning",
      duration: duration
    });
  },
  //系统级消息
  saySysInfo(message) {
    Notification({
      title: "消息",
      message: message,
      type: "info",
      duration: duration
    });
  },
  //系统级成功
  saySysSuccess(message) {
    Notification({
      title: "成功",
      message: message,
      type: "success",
      duration: duration
    });
  },
  //系统级错误
  saySysError(message) {
    Notification({
      title: "错误",
      message: message,
      type: "error",
      duration: duration
    });
  },
  //操作级警告
  sayOpWarn(message) {
    Message({
      title: "警告",
      message: message,
      type: "warning",
      duration: duration
    });
  },
  //操作级消息
  sayOpInfo(message) {
    Message({
      title: "消息",
      message: message,
      type: "info",
      duration: duration
    });
  },
  //操作级成功
  sayOpSuccess(message) {
    Message({
      title: "成功",
      message: message,
      type: "success",
      duration: duration
    });
  },
  //操作级错误
  sayOpError(message) {
    Message({
      title: "错误",
      message: message,
      type: "error",
      duration: duration
    });
  }
};



